import React from "react";
import { Route, Switch } from "react-router-dom";
import "./App.css";
import Landingpage from "./components/Page/Landingpage";

function App() {

  return (
    <div className="App">
      <Switch>
      <Route path='/' exact>
        <Landingpage />
      </Route>
      <Route path='exp-chart' exact>
        <Landingpage />
      </Route>
      </Switch>
    </div>
  );
}

export default App;



